.switcher{
  position: relative;

  margin: 8px 13px;

  background-color: #ffffff;
  border: 1px solid #CDCDCD;
  border-radius: 6px;

  &::before{
    content: '';

    position: absolute;
    top: 3px;
    left: 6px;

    height: 6px;
    width: 35px;

    border-radius: 6px;
    background-color: $color-accent;
  }

  &--active::before{
    left: auto;
    right: 6px;
  }
}
